import "./App.css";
import Typography from "@mui/material/Typography";
import Divider from "@mui/material/Divider";
import CloudIcon from "@mui/icons-material/Cloud";
import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import { useEffect, useState } from "react";
import Grid from "@mui/material/Grid2";
import axios from "axios";
import { useTranslation } from "react-i18next";
import moment from "moment";
import "moment/min/locales";
moment.locale("ar");

let cancelaxios = null;

function App() {
  const { t, i18n } = useTranslation();
  const [Temp, setTemp] = useState(null);
  const [DataAndTime, setDataAndTime] = useState({
    day: null,
    date: null,
  });
  const [error, setError] = useState(null);
  const [local, setlocal] = useState(localStorage.getItem("language") || "ar");
  const [city, setCity] = useState("");

  function handleChangelanguage() {
    const language = local === "en" ? "ar" : "en";
    setlocal(language);
    moment.locale(language);
    localStorage.setItem("language", language);
  }

  useEffect(() => {
    i18n.changeLanguage(local);
    moment.locale(local);
    setDataAndTime({
      day: moment().format("dddd"),
      date: moment().format("LL"),
    });
  }, [local]);

  const fetchWeather = (cityName) => {
    axios
      .get(
        `https://api.openweathermap.org/data/2.5/weather?q=${cityName}&appid=2f825fb6c09e7ffd72799a6eaf5b246d`,
        {
          cancelToken: new axios.CancelToken((c) => {
            cancelaxios = c;
          }),
        }
      )
      .then((response) => {
        const TempResponse = Math.round(response.data.main.temp - 273.15);
        const Temp_max = Math.round(response.data.main.temp_max - 273.15);
        const Temp_min = Math.round(response.data.main.temp_min - 273.15);
        const discription = response.data.weather[0].description;
        const ResponseIcon = response.data.weather[0].icon;
        const Name = response.data.name;

        const weatherData = {
          TempResponse,
          Temp_max,
          Temp_min,
          discription,
          ResponseIcon,
          Name,
        };

        localStorage.setItem("weatherData", JSON.stringify(weatherData));
        setTemp(weatherData);
        setError(null);
      })
      .catch((error) => {
        setTemp(null);
        setError(t("City not found"));
        console.log(error);
      });
  };

  function handleCitySubmit() {
    fetchWeather(city);
    setCity("");
  }

  useEffect(() => {
    const storedWeatherData = localStorage.getItem("weatherData");
    if (storedWeatherData) {
      setTemp(JSON.parse(storedWeatherData));
    } else {
      fetchWeather("Alexandria"); // يمكنك تحديد مدينة افتراضية
    }
    return () => {
      if (cancelaxios) {
        cancelaxios();
      }
    };
  }, []);

  return (
    <div className="App">
      {/* textField */}
      <>
        <TextField
          dir={local === "ar" ? "rtl" : "ltr"}
          label={t("Enter city with English")}
          variant="outlined"
          value={city}
          onChange={(e) => setCity(e.target.value)}
          style={{
            marginBottom: "10px",
            width: "100%",
            borderRadius: "3px",
          }}
          InputLabelProps={{
            style: { color: "white" },
          }}
          sx={{
            "& .MuiOutlinedInput-root": {
              "&:hover fieldset": {
                borderColor: "white", // لون الحدود عند الـ hover
              },
              "& .MuiOutlinedInput-input:focus": {
                color: "white",
              },
              "& .Mui-focused fieldset": {
                borderColor: "white",
              },
              "& .MuiOutlinedInput-notchedOutline": {
                borderColor: "white",
              },
            },
          }}
        />

        <Button
          variant="outkine"
          onClick={handleCitySubmit}
          style={{ marginBottom: "10px", border: "1px solid " }}
        >
          {t("Get Weather")}
        </Button>
      </>

      {/* card */}
      {Temp && (
        <div
          style={{
            direction: local === "ar" ? "rtl" : "ltr",
            width: "100%",
            border: "1px solid white",
            backgroundColor: "#0059b3",
            padding: "15px",
            borderRadius: "20px",
            boxShadow: "13px 10px 15px rgba(0, 0, 0, 0.2)",
            transition: "all 0.5s linear",
          }}
        >
          {/* header */}
          <Grid
            container
            spacing={2}
            alignItems="center"
            justifyContent="space-around"
          >
            <Grid item xs={12} md={8}>
              <Typography
                variant="h2"
                sx={{ fontSize: { xs: "35px", md: "50px" } }}
              >
                {t(Temp.Name)}
              </Typography>
            </Grid>
            <Grid item xs={12} md={4}>
              <div style={{ textAlign: "center" }}>
                <Typography variant="h6">{DataAndTime.day}</Typography>
                <Typography variant="h6">{DataAndTime.date}</Typography>
              </div>
            </Grid>
          </Grid>

          <Divider style={{ backgroundColor: "white" }} />

          {/* body */}
          <Grid
            container
            spacing={2}
            alignItems="center"
            justifyContent="space-around"
            useTranslation="all 0.5s linear"
          >
            {/* العمود الأول - درجة الحرارة والصورة والوصف */}
            <Grid item xs={12} md={8}>
              <Grid
                container
                spacing={4}
                alignItems="center"
                sx={{
                  justifyContent: { xs: "center", md: "flex-start" },
                }}
              >
                <Typography variant="h1">{Temp.TempResponse}</Typography>
                <img
                  src={`https://openweathermap.org/img/wn/${Temp.ResponseIcon}@2x.png`}
                  alt="Logo"
                  style={{ width: "50px", borderRadius: "50%" }}
                />
              </Grid>
              <Grid
                container
                alignItems="center"
                sx={{
                  justifyContent: { xs: "center", md: "flex-start" },
                }}
              >
                <Typography variant="h6" style={{ margin: "10px 0" }}>
                  {t(Temp.discription)}
                </Typography>
              </Grid>
              <Grid container spacing={2} justifyContent="center">
                {/* أدنى درجة حرارة */}
                <Grid item xs={12} md={5}>
                  <Typography variant="h6">
                    {t("temp_min")} : {Temp.Temp_min}
                  </Typography>
                </Grid>

                {/* الفاصل العمودي */}
                <Grid
                  item
                  xs={12}
                  md={2}
                  sx={{ display: { xs: "none", md: "block" } }}
                >
                  <Typography variant="h6" style={{ textAlign: "center" }}>
                    |
                  </Typography>
                </Grid>

                {/* أعلى درجة حرارة */}
                <Grid item xs={12} md={5}>
                  <Typography variant="h6">
                    {t("temp_max")} : {Temp.Temp_max}
                  </Typography>
                </Grid>
              </Grid>
            </Grid>

            {/* العمود الثاني - رمز السحابة */}
            <Grid item xs={12} md={4}>
              <div style={{ textAlign: "center" }}>
                <CloudIcon sx={{ fontSize: 100 }} />
              </div>
            </Grid>
          </Grid>
        </div>
      )}
      {error && (
        <div
          style={{
            color: "white",
            marginTop: "10px",
            textAlign: "center",
          }}
        >
          <Typography variant="h6">{error}</Typography>
        </div>
      )}
      {Temp && (
        <div
          style={{ width: "100%", direction: local === "ar" ? "ltr" : "rtl" }}
        >
          <Button
            variant="outlined"
            style={{
              color: "white",
              border: "1px solid white",
              marginTop: "10px",
              display: "flex",
              // justifyContent: "end",
              boxShadow: "5px 5px 10px rgba(0, 0, 0, 0.4)",
            }}
            onClick={handleChangelanguage}
          >
            {local === "ar" ? "English" : "العربيه"}
          </Button>
        </div>
      )}
      {/* card */}
    </div>
  );
}

export default App;
